body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-grid-container {
  display: grid;
  grid-template-columns: 1fr;
}
.overlay {
  position: absolute;
  width: 100vw;
  height: calc(100vw * 9 / 16);
  top: 0;
}

.player-wrapper {
  /* background-color: #111; */
  /* filter: opacity(.5); */
  /* filter: invert(.5); */
  position: relative;
  padding-top: calc(100%/(568/320)) /* Player ratio: 100 / (1280 / 720) */
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}
.logo {
  height: 80px;
  z-index: 2;
}

.logo:hover {
  cursor: pointer;
  -webkit-filter: brightness(150%);
          filter: brightness(150%);
}

.nav-wrapper {
  position: absolute;
  bottom: 10vh;
  right: 10vh;
}
.Header {
  position: relative;
}
.elevate-logo {
  width: 400px;
  padding-right: 80px;
}

.sticky-nav-wrapper {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 40px;
  z-index: 5;
}

.sticky-nav-wrapper > * {
  width: 300px;
}
.Us {
  /* height: 100vh; */
  min-height: 900px;
}

h2 {
  font-size: 44px;
}

p {
  font-size: 32px;
}

.flex-center {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.parallax-content {
  position: relative;
}

.sticky-nav-Us {
  /* color: #fff; */
}

.us {
  position: relative;
}

.us-sticky {
  position: -webkit-sticky;
  position: sticky;
  right: 20px;
  top: 20px;
  z-index: 2;
}

.us-wrapper {
  color: #fff;
  background-image: url(/elevate-innovate-dev/static/media/stock-teamwork.59f028e6.png);
  background-repeat: no-repeat;
  background-size: 100%;
  font-family: 'Montserrat';
  height: calc( 100vw * 2 / 3);
  padding: 0 300px;
  position: relative;
}

.us-wrapper > * {
  text-align: center;
}
.Design {
  position: relative;
}

.center {
  align-items: center;
  display: flex;
  justify-content: center;
}

.design-grid {
  display: grid;
  grid-template-columns: 450px 1fr;
  padding: 0 5vw;
}

.design-img-small {
  width: 100%;
}

.design-img-big {
  width: 100%;
}

.floater-samples {
  height: 400px;
}

.grey-goose {
  grid-row: span 3;
}
.Clients {
  height: 100vh;
  min-height: 1000px;
}

.clients-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 60px;
  margin-left: 200px;
  margin-right: 200px;
}

.clients-grid > * {
  width: 100%;
}

.clients-title {
  display: flex;
  justify-content: center;
}

.logo {
  max-height: 12vw;
  max-width: 12vw;
  height: auto;
  width: auto;
}

.logo-container {
  align-items: center;
  display: flex;
  justify-content: center;
}
.Contact {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  height: 100vh;
  min-height: 640px;
  justify-content: center;
  padding-left: 200px;
}

.contact-address {
  margin:  40px 0;
}

.contact-address > p {
  margin: 0;
}

.contact-links {
  margin: 40px 0;
}

.contact-links > a {
  font-size: 32px;
  margin-right: 10px;
  text-decoration: none;
}

.contact-links > a:visited {
  color: #0000ee;
}
.site-background {
  background-image: url(/elevate-innovate-dev/static/media/blueprint.6855a9b7.jpg);
  background-size: 100vw 100vh;
  height: 100vh;
  -webkit-filter: opacity(.1);
          filter: opacity(.1);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: -99;
}
